body {
    font-family: system-ui;
}

main {
    padding: 2em 0;
}

footer {
    margin: 2em 0;
}

.gallery {
    display: flex;
    flex-direction: column;
    gap: 2em;
}

.demo {
    display: flex;
    gap: 1em;
}

.demo .left {
    flex-basis: 300px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.demo h2 {
    margin: 0;
}

.demo canvas {
    border: 2px dotted;
    flex-grow: 1;
    max-width: 800px;
}

.config {
    bottom: 0;
    line-height: 1.5;
}

.config .property {
    display: inline-flex;
    align-items: center;
    gap: 1ch;
}

.config .value {
    font-weight: bold;
}
